<template>
    <div class="wrap">
        <router-view></router-view>
    </div>
</template>
<style>
  * {
    padding: 0;
    margin: 0;
    text-decoration: none;
  }

  html, body,.wrap {
    width: 100%;
    height: 100%;
  }
  body {
    background-color: #fff;
    position: relative;
    font-size: 16px;
  }
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    height: 100%;
  }
  /**/
  ::-webkit-scrollbar{width:8px;} /*width,height,background,border*/
  ::-webkit-scrollbar-button{display:none;} /*display:none*/
  ::-webkit-scrollbar-track{display:none} /*display:none*/
  ::-webkit-scrollbar-track-piece{display:none;}  /**/
  ::-webkit-scrollbar-thumb{background:#cecfd3;border-radius:6px;opacity: 0.85;}  /**/
  ::-webkit-scrollbar-corner{}  /* */
  ::-webkit-resizer{}

  button{
    background:none;
    border:0;
  }
</style>
<script>
    import 'babel-polyfill'
    import router from 'src/router'
    import store from 'src/vuex'
    import filters from 'src/filters'
    import 'src/lib/jsBridge'

    export default {
        router,
        store,
        filters,
        name: 'root',
        components: {},
        created () {
            this.$router.replace({
                path: 'Lesson'
//                path: 'Bookcase'
            })
        }
    }
</script>



